body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, p {
  text-align: center;
  margin-top: 1.2em;
}

.memory {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 300px;
  margin: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.memory > .card {
  font-size: 2em;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-flex-basis: calc(100% / 6 - 0.6em);
          flex-basis: calc(100% / 6 - 0.6em);
  outline: 0.08em solid silver;
  margin: 0.2em;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
}

.memory > .card.hidden {
  background: silver;
}

.memory > .card.justMatched,
.memory > .card.justMismatched {
  outline: 0.1em solid green;
}
.memory > .card.justMismatched {
  outline-color: red;
}

.memory > .card.visible {
  cursor: not-allowed;
}

.memory > .card > .symbol {
  margin: auto;
}

.memory > .guesses {
  font-size: 1em;
  width: calc(100% - 0.4em);
  margin-top: 1em;
  margin-bottom: 0.2em;
  font-family: Menlo, Monaco, Consolas, Inconsolata, 'Courier New', monospace;
}

.memory > .guesses em {
  color: #309286;
}

.hallOfFame {
  width: 100%;
  border-collapse: collapse;
}

.hallOfFame .date,
.hallOfFame .guesses {
  width: 20%;
  text-align: center;
}

.hallOfFame tr:nth-child(even) {
  background: #eee;
}

.highScoreInput {
  font-size: 1em;
  width: calc(100% - 0.4em);
  margin: 0.2em;
}
.highScoreInput input {
  display: block;
  width: 100%;
  margin-bottom: 0.2em;
  font-family: Menlo, Monaco, Consolas, Inconsolata, 'Courier New', monospace;
}

